<template>

    <div id="tranlateOutput" class="col-md-6  col-sm-6 col-md-offset-3 col-sm-offset-3 ">

        <h1 >{{translatedText}}</h1>


    </div>
</template>

<script>
    export default {
        name: "tranlateOutput",
        props: [
            'translatedText'
        ]
    }
</script>

<style>
    #tranlateOutput{
        font-size: 24px;
        border: 1px solid #99f;
        border-radius: 10px;
        height: 150px;
        padding-top: 40px;
    }
</style>


