<template>
    <div id="translateForm" class="row">
        <div class="col-md-6  col-sm-6 col-md-offset-3 col-sm-offset-3 ">
            <form v-on:submit="formSubmit" class="well form-inline-block " id="transForm">

                <input id="inp" type="text" v-model="textToTranslate" placeholder="输入需要翻译的内容"
                       class="form-control input-lg">

                <select v-model="language" class="form-control">
                    <option value="en">English</option>
                    <option value="ru">Russian</option>
                    <option value="ko">Korean</option>
                    <option value="ja">Japanese</option>
                </select>
                <input type="submit" value="翻译" class="btn btn-primary" id="btn">
            </form>
        </div>
    </div>
</template>

<script>

    export default {
        name: "translateForm",
        data() {
            return {
                textToTranslate: '',
                language: ''
            }
        },
        methods: {
            formSubmit(e) {
                this.$emit('formSubmit', this.textToTranslate, this.language);
                e.preventDefault();
            }
        },
        created() {
            this.language = 'en';
        }
    }
</script>

<style>
    #transForm {
        border: 1px solid #ccc;
        border-radius: 10px;
    }

    #btn {
        margin-top: 10px;
    }

</style>


